*,
*::after,
*::before {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

button {
    border: none;
    cursor: pointer;
}

ul li {
    list-style: none;
}

input,
textarea {
    outline: none;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}